/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.bg {
  z-index: -1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../img/bg-image.avif);
}
@media (min-height: 1px) and (max-height: 840px) {
  .bg {
    bottom: -15rem;
  }
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.juego {
  position: relative;
}
.juego h1 {
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .juego h1 {
    font-size: 5rem;
  }
}
.juego__heading, .juego__contenedor {
  background-color: #0b0617;
  color: #FFFFFF;
  width: 90%;
  text-align: center;
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translate(-50%);
  padding: 1rem;
}
@media (min-width: 768px) {
  .juego__heading, .juego__contenedor {
    width: 75%;
    padding: 2rem;
  }
}
@media (min-width: 768px) {
  .juego__heading {
    top: 5rem;
  }
}
.juego__contenedor {
  top: 16rem;
  height: 60rem;
}
@media (min-width: 768px) {
  .juego__contenedor {
    top: 25rem;
    height: 55rem;
  }
}

@media (min-width: 768px) {
  .formulario {
    width: 100%;
  }
}
.formulario__legend {
  margin: 1rem 0 2rem 0;
  color: #c5fe60;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .formulario__legend {
    font-size: 2rem;
  }
}
@media (min-width: 768px) {
  .formulario__row {
    display: flex;
  }
}
.formulario__campo {
  margin: 1rem 1rem 2rem;
}
@media (min-width: 768px) {
  .formulario__campo {
    width: 100%;
  }
}
.formulario__label {
  display: block;
  text-align: start;
  margin-bottom: 1rem;
}
.formulario__input {
  width: 100%;
  padding: 1rem;
}
.formulario__submit {
  background-color: #c5fe60;
  padding: 1rem 3rem;
  color: #0b0617;
  margin: 1rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #b3fe2d;
}

.alerta, .correct {
  background-color: #b40000;
  padding: 1rem;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .alerta, .correct {
    font-size: 2rem;
  }
}

.correct {
  background-color: rgba(0, 180, 54, 0.815);
}

.campo-trivia {
  text-align: left;
  margin: 0 0 2rem 2rem;
}
.campo-trivia input {
  margin-right: 2rem;
}
.campo-trivia input:after {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: #d1d3d1;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 3px solid white;
}
.campo-trivia input:checked:after {
  width: 15px;
  height: 15px;
  border-radius: 15px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: #7ec401;
  content: "";
  display: inline-block;
  visibility: visible;
  border: 3px solid white;
}
.campo-trivia label {
  cursor: pointer;
}

.question-text {
  margin-bottom: 5rem;
  line-height: 1.5;
}

.questionFieldset {
  border: none;
}

.button__disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.div-felicidades {
  margin-top: 15rem;
}
.div-felicidades p {
  margin-bottom: 5rem;
}
.div-felicidades span {
  color: #c5fe60;
  font-weight: bold;
  font-size: 2.5rem;
}

.div-disculpa {
  margin-top: 15rem;
}
.div-disculpa p {
  font-size: 2.5rem;
}